<template>
  <div class="behospitalizedadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12">
                <v-alert outlined color="success">
                  <v-row>
                    <v-col cols="12" md="4">床號: {{list.bname}}</v-col>
                    <v-col cols="12" md="4">護字號: {{list.p_code}}</v-col>
                    <v-col cols="12" md="4">姓名: {{list.uname}}</v-col>
                  </v-row>
                </v-alert>
              </v-col>
              <v-col cols="12" md="3">
                <datepicker :label="'住院日期(開始)'" v-model="list.start_day"></datepicker>
              </v-col>
              <v-col cols="12" md="3">
                <v-text-field label="住院時間(開始)" type="time" v-model="list.start_time"></v-text-field>
              </v-col>
              <v-col cols="12" md="3" class="d-flex">
                <datepicker :label="'住院日期(結束)'" v-model="list.end_day"></datepicker>
                <v-icon @click="list.end_day = '0000-00-00'">mdi-window-close</v-icon>
              </v-col>
              <v-col cols="12" md="3" class="d-flex">
                <v-text-field label="住院時間(結束)" type="time" v-model="list.end_time"></v-text-field>
                <v-icon @click="list.end_time = '00:00:00'">mdi-window-close</v-icon>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import datepicker from "@/components/DatePicker";

export default {
  components: { datepicker },
  data() {
    return {
      dialog: false,
      list: { }, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"]
    };
  },
  props: ["edititem"],
  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function() {
      this.dialog = true;
      this.editProcess();
    });
  },
  model: {
    prop: "edititem"
    // event: "parentData"
  },
  updated() {
    // if (this.list.username == "" || this.list.username == undefined) {
    //   this.$nextTick(() => {
    //     this.$refs.username.focus();
    //     this.$refs.form.resetValidation();
    //   });
    // }
  },
  watch: {
    edititem: [
      {
        handler: "editProcess"
      }
    ]
  },
  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增人員";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {};
    },
    editProcess() {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改住院紀錄資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = this.edititem; //將頁面資料與父項的資料連結
      this.list = { ...this.edititem }; //複制一份實際修改資料內容

      if (this.list.end_day == '0000-00-00'){
        this.list.end_day = '';
      }
    },
    // addOK() {
    //   //確認新增
    //   if (this.$refs.form.validate()) {
    //     this.$store.commit("getTime"); //取得現在時間
    //     this.list["create_man"] =
    //       this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

    //     this.axios
    //       .post(
    //         "general/add/" + this.$store.state.databaseName + "/personnel",
    //         qs.stringify(this.list)
    //       )
    //       .then(rs => {
    //         if (rs.data.state == 1) {
    //           var pop = { msg: "已新增", type: true, theme: "error" };
    //           this.$store.commit("snackbar", pop);
    //           this.list = {};
    //           this.dialog = false;
    //           this.$emit("getAllData");
    //           // this.$refs.form.resetValidation();
    //         }
    //       });
    //   }
    // },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.axios
          .post("bed_used/edit/" + this.$store.state.databaseName + "/behospitalized",qs.stringify(this.list))
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
            }
          });
      }
    }
  }
};
</script>

<style lang="scss">
// .behospitalizedadd{
//   font-family: '微軟正黑體';
// }
</style>