<template>
  <div class="behospitalizedlist">
    <v-row>
      <v-col cols="12" md="6">
        <h2 class="subheading grey--text">
          <v-icon class="mx-1">mdi-card-bulleted-outline</v-icon>
          <span>住院紀錄管理</span>
        </h2>
      </v-col>
      <v-col cols="12" md="6">
        <!-- 功能表 -->
        <v-toolbar dense>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model="serachKey"
            @keyup.stop="search()"
          ></v-text-field>

          <popupadd @getAllData="getAllData" ref="editProcess" v-model="edititem" v-show="false"></popupadd>
                   
        </v-toolbar>
      </v-col>
    </v-row>

    <!-- 表單內容 -->
    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
        <v-btn class="mx-2" @click.stop="goBed">顯示床位管理</v-btn>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="viewData"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
          locale="zh-tw"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="mt-2 text-no-wrap" height="68vh">
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left"></th>
                    <th class="text-left">床號</th>
                    <th class="text-left">護字號</th>
                    <th class="text-left">姓名</th>
                    <th class="text-left">住院日期(開始)</th>
                    <th class="text-left">住院時間(開始)</th>
                    <th class="text-left">住院日期(結束)</th>
                    <th class="text-left">住院時間(結束)</th>
                    <th class="text-left">創建人員</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item,index) in props.items" :key="index">
                    <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn color="primary" dark v-on="on">功能</v-btn>
                        </template>
                        <v-list>
                          <v-list-item @click="edit(item)">
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item @click="del(item)">
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    <td>{{item.bname}}</td>
                    <td>{{item.p_code}}</td>
                    <td>{{item.uname}}</td>
                    <td>{{item.start_day}}</td>
                    <td>{{item.start_time}}</td>
                    <td>{{item.end_day | filterDate }}</td>
                    <td>{{item.end_time | filterTime }}</td>
                    <td>{{item.create_man}}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
    </v-card>

    <!-- <pre>{{viewData}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/main/BehospitalizedAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10
    };
  },

  filters:{
    filterDate(val){
      if (val == '0000-00-00'){
        return '住院中';
      }else{
        return val;
      }
    },
    filterTime(val){
      if (val == '00:00:00'){
        return '';
      }else{
        return val;
      }
    },
  },

  created() {
    this.getAllData();
  },

  methods: {
    getAllData() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("bed_used/getAll/" + this.$store.state.databaseName + "/behospitalized")
        .then(rs => {
          // console.log(rs.data)
          this.items = this.viewData = rs.data;
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    edit(item) {
      //修改功能
      this.edititem = item;
      this.$refs.editProcess.$emit("fromParent");
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        if (res) {
          this.$store.commit("getTime"); //取得現在時間
          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "behospitalized",
            info: JSON.stringify(delData)
          };

          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/behospitalized",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    },
    goBed() {
      this.$router.push("/Main/bedlist");
    }
  }
};
</script>


<style lang="scss">
.behospitalizedlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>